.advantages__block_big {
	flex: 0 0 60%;
	padding-bottom: 29px;
}
.advantages__block_lg {
	position: relative;
	flex: 0 0 30%;
	&::before {
		content: "";
		position: absolute;
		z-index: 2;
		left: -50px;
		top: 0;
		height: calc(100% + 92px);
		width: 2px;
		background-color: $red-accent;
