// .algorithm////////////////////////////////////////////////////////////
.algorithm {
	margin-top: 100px;
	margin-bottom: 100px;
}
.algorithm__list {
	margin-top: 90px;
}
.algorithm__item {
	display: flex;
	gap: 40px;
	&:not(:last-child) {
		margin-bottom: 90px;
	}
	&_third {
		flex-wrap: wrap;
	}
}

.algorithm__text {
	color: #bdbdbd;
	font-size: 20px;
	line-height: 1.55;
	font-weight: 400;
	&_first {
		flex: 0 0 265px;
	}
	&_third {
		flex: 0 0 665px;
	}
}

.algorithm__img {
	position: relative;
	flex: 0 0 300px;
	overflow: hidden;
	&_first {
		flex: 0 0 360px;
		align-items: stretch;
		min-height: 180px;
	}
	&_second {
		flex: 0 0 665px;
		align-items: stretch;
		min-height: 300px;
	}
	&_third {
		flex: 0 0 100%;
		min-height: 380px;
	}
	& img {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

.algorithm__title {
	font-size: 30px;
	&_second {
		margin-bottom: 30px;
	}
}

.algorithm__link {
	display: block;
	width: 400px;
	margin-left: auto;
	margin-right: 23%;
}

@media (max-width: 1025px) {
	.algorithm {
		margin-top: 50px;
	}
	.algorithm__list {
		margin-top: 50px;
	}
	.algorithm__text {
		&_first {
			flex: 0 0 370px;
		}
		&_third {
			flex: 0 0 665px;
		}
	}
	.algorithm__img {
		&_first {
			flex: 0 0 300px;
		}
		&_second {
			flex: 0 0 400px;
			min-height: 200px;
		}
		&_third {
			min-height: 300px;
		}
	}
	.algorithm__title {
		flex: 0 0 90px;
	}
	.algorithm__link {
		margin-left: 0;
	}
}

@media (max-width: 900px) {
	.algorithm__item {
		flex-direction: column;
		row-gap: 20px;
		&:not(:last-child) {
			margin-bottom: 60px;
		}
		&_third {
			flex-direction: column-reverse;
		}
	}

	.algorithm__text {
		flex: auto;
	}
	.algorithm__img {
		flex: 0 0 200px;
		min-height: 200px;
		&_first {
			order: -2;
			flex: auto;
		}
	}
	.algorithm__title {
		flex: auto;
		&_first {
			order: -1;
		}
	}
