.team-slide__img {
	position: relative;
	flex: 0 0 303px;
	height: 353px;
	overflow: hidden;
	& img {
		position: absolute;
		left: 0;
		right: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	&::after {
		content: "";
		position: absolute;
		top: 40px;
		left: 0;
		width: 9px;
		height: 270px;
		background-color: $red-accent;
	}
}

.team-slide__name {
	color: #fff;
	font-size: 32px;
	font-weight: 600;
	line-height: 35.2px;
}
.team-slide__post {
	margin: 16px 0 37px;
	color: #fff;
	font-size: 16px;
	font-style: normal;
	font-weight: 400;
	line-height: 17.6px;
}
.team-slide__descr {
	font-size: 20px;
	font-weight: 300;
}

.team-slider {
	position: relative;
	width: 100%;
	overflow: hidden;
}

.team-slider__wrap {
	display: flex;
	width: 100%;
	height: 100%;
}

.team-slide {
	flex-shrink: 0;
}

.team-slider > .swiper-pagination-bullets {
	display: flex;
	width: 283px;
	height: 4px;
	left: calc(100% - 283px - 100px);
	bottom: 11px;
	background-color: #191919;
}

.swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet {
	margin: 0;
	height: 4px;
	border-radius: 0;
	flex-grow: 1;
}

.swiper-pagination-bullet-active {
	background-color: $red-accent;
}

.team-slider .swiper-button-prev,
.team-slider .swiper-button-next {
	width: 20px;
	height: 20px;
	background-color: transparent;
	top: calc(100% - 3px);
}

.team-slider .swiper-button-prev {
	left: calc(100% - 60px);
}
.team-slider .swiper-button-next {
	left: calc(100% - 20px);
}
.team-slider .swiper-button-prev::after,
.team-slider .swiper-button-next::after {
	width: 100%;
	height: 100%;
	background-repeat: no-repeat;
	background-size: contain;
	font-size: 0;
}

.team-slider .swiper-button-prev::after {
	background-image: url(../img/icons/arrow-left.svg);
}
.team-slider .swiper-button-next::after {
	background-image: url(../img/icons/arrow-right.svg);
}

@media (max-width: 900px) {
	.team__descr-wrap {
		flex-direction: column;
		row-gap: 25px;
		margin-bottom: 70px;
	}
	.team__link {
		align-self: flex-start;
	}
	.team-slide__wrap {
		position: relative;
		display: block;
		padding-top: 100px;
	}
	.team-slide__img {
		width: 312px;
	}
	.team-slide__name {
		position: absolute;
		top: 0;
		left: 0;
	}
	.team-slide__post {
		position: absolute;
		top: 30px;
		left: 0;
	}
	.team-slide__descr {
		margin-top: 26px;
		min-height: 180px;
	}
	.team-slider .slider-buttons {
		display: none;
	}
	.team-slider > .swiper-pagination-bullets {
		left: 0;
	}
}

